<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar" alt="">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div class="support" v-if="seller.supports">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div class="support-count" v-if="seller.supports" @click="showDetail">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper" @click="showDetail">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" alt="" width="100%" height="100%">
    </div>
    <transition name="fade">
      <div class="detail" v-show="detailShow">
        <div class="detail-wrapper clearfix">
          <div class="detail-main">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wrapper">
              <star :size="48" :score="seller.score"></star>
            </div>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul class="supports">
              <li class="support-item" v-for="item in seller.supports">
                <span class="icon" :class="classMap[item.type]"></span>
                <span class="text">{{item.description}}</span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="detail-close" @click="hideDetail">
          <i class="icon-close"></i>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import star from '../star/star'

  export default {
    name: 'header',
    data () {
      return {
        classMap: [],
        detailShow: false
      }
    },
    methods: {
      showDetail () {
        this.detailShow = true
      },
      hideDetail () {
        this.detailShow = false
      }
    },
    props: {
      seller: {
        type: Object
      }
    },
    components: {
      star
    },
    created () {
      this.classMap = ['decrease', 'discount', 'guarantee', 'special', 'invoice']
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .fade-enter {
    opacity: 0
  }

  .fade-leave {
    opacity: 1
  }

  .fade-enter-active {
    transition: all 0.5s ease-in-out;
  }

  .fade-leave-active {
    opacity: 0;
    transition: all 0.5s ease-in-out;
  }

  .header {
    color: #FFF;
    position: relative;
    background: rgba(7, 17, 27, 0.5);
    overflow: hidden;
    .content-wrapper {
      font-size: 0;
      padding: 24px 12px 18px 24px;
      position: relative;
      .avatar {
        display: inline-block;
        vertical-align: top;
        img {
          border-radius: 2px;
        }
      }
      .content {
        font-size: 14px;
        margin-left: 16px;
        display: inline-block;
        .title {
          margin: 2px 0 8px 0;
          .brand {
            width: 30px;
            height: 18px;
            display: inline-block;
            bg-image('brand');
            background-size: 30px 18px;
            background-repeat: no-repeat;
            vertical-align: top;
          }
          .name {
            font-size: 16px;
            margin-left: 6px;
            font-width: bold;
            line-height: 18px;
          }
        }
        .description {
          font-size: 12px;
          line-height: 12px;
          margin-bottom: 10px;
        }
        .support {
          font-size: 0;
          .icon {
            vertical-align: top;
            background-size: 12px 12px;
            display: inline-block;
            width: 12px;
            height: 12px;
            background-repeat: no-repeat;
            margin-right: 4px;
            &.decrease {
              bg-image('decrease_1');
            }
            &.discount {
              bg-image('discount_1');
            }
            &.guarantee {
              bg-image('guarantee_1');
            }
            &.invoice {
              bg-image('invoice_1');
            }
            &.special {
              bg-image('special_1');
            }
          }
          .text {
            font-size: 10px;
          }
        }
      }
      .support-count {
        position: absolute;
        right: 12px;
        bottom: 18px;
        padding: 0 8px;
        height: 24px;
        line-height: 24px;
        border-radius: 14px;
        background: rgba(0, 0, 0, 0.2);
        text-align: center;
        .count {
          font-size: 10px;
          vertical-align: top;
        }
        .icon-keyboard_arrow_right {
          font-size: 10px;
          line-height: 24px;
          margin-left: 2px;
        }
      }
    }
    .bulletin-wrapper {
      height: 28px;
      line-height: 28px;
      padding: 0 22px 0 12px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      position: relative;
      background: rgba(7, 17, 27, 0.2);
      .bulletin-title {
        display: inline-block;
        width: 22px;
        height: 12px;
        bg-image('bulletin');
        background-size: 22px 12px;
        background-repeat: no-repeat;
        vertical-align: middle;
      }
      .bulletin-text {
        vertical-align: middle;
        margin: 0 4px;
        font-size: 10px;
      }
      i {
        position: absolute;
        font-size: 10px;
        right: 12px;
        top: 8px;
      }
    }
    .background {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      filter: blur(10px);
    }
    .detail {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 100;
      width: 100%;
      height: 100%;
      overflow: auto;
      background: rgba(7, 17, 27, 0.8);
      -webkit-backdrop-filter:blur(10px);
      &.fade-enter {
        opacity: 0
      }

      &.fade-leave {
        opacity: 1
        background: rgba(7, 17, 27, 0.8);
      }

      &.fade-enter-active {
        transition: all 0.5s ease-in-out;
      }

      &.fade-leave-active {
        opacity: 0;
        background: rgba(7, 17, 27, 0);
        transition: all 0.5s ease-in-out;
      }
      .detail-wrapper {
        min-height: 100%;
        width: 100%;
        .detail-main {
          margin-top: 64px;
          padding-bottom: 64px;
          .name {
            font-size: 16px;
            line-height: 16px;
            text-align: center;
            font-weight: 700;
          }
          .star-wrapper {
            margin-top: 18px;
            padding: 2px;
            text-align: center;
          }
          .title {
            width: 80%;
            margin: 30px auto;
            display: flex;
            .line {
              flex: 1; //等分
              border-bottom: 1px solid rgba(255, 255, 255, 0.2);
              top: -6px;
              position: relative;
            }
            .text {
              padding: 0 12px;
              font-size: 14px;
              font-weight: 700;
            }
          }
          .supports {
            width: 80%;
            margin: 0 auto;
            .support-item {
              padding: 0 12px;
              margin-bottom: 12px;
              font-size: 0;
              &:last-child {
                margin-bottom: 0;
              }
              .icon {
                vertical-align: top;
                background-size: 16px 16px;
                display: inline-block;
                width: 16px;
                height: 16px;
                background-repeat: no-repeat;
                margin-right: 6px;
                &.decrease {
                  bg-image('decrease_2');
                }
                &.discount {
                  bg-image('discount_2');
                }
                &.guarantee {
                  bg-image('guarantee_2');
                }
                &.invoice {
                  bg-image('invoice_2');
                }
                &.special {
                  bg-image('special_2');
                }
              }
              .text {
                font-size: 12px;
                line-height: 16px;
              }
            }
          }
          .bulletin {
            width: 80%;
            margin: 0 auto;
            .content {
              padding: 0 12px;
              font-size: 12px;
              line-height: 24px;
            }
          }
        }
      }
      .detail-close {
        position: relative;
        width: 32px;
        height: 32px;
        margin: -64px auto 0;
        clear: both;
        font-size: 32px;
      }
    }
  }
</style>



